import * as mediasoup from 'mediasoup-client';

import { EnhancedEventEmitter } from './common-js/EnhancedEventEmitter';

import { TSDPInfo } from 'types/dist/common.types';
import { TTransportType } from 'types/dist';

import type {
  AppData,
  TransportOptions,
  Producer,
  ConnectionState,
  DataProducer,
  DataConsumer,
} from 'mediasoup-client/lib/types';

import { mainLogger } from './logger';
import Socket from './Socket';
import Consumer from './Consumer';
import { getMediaStreamKind, getMediaTrack } from './helpers';
import {
  TConsumeResponseData,
  TConsumeDataResponseData,
} from 'types/dist/mq/response.types';
import {
  codecOptionsViaKind,
  encodingViaMediaType,
} from './constants/rtpConstants';
import { EnhancedMap } from './common-js/EnhancedMap';

const logger = mainLogger.createSubLogger('Transport');

export type TransportEvents = {
  connectTransportResponse: [];
};

class Transport extends EnhancedEventEmitter<TransportEvents> {
  /**
   * Peer Id, which handles the peer id.
   */
  public readonly peerId: string;

  /**
   *  MediaSoup Device Instance, which handles the browsers or mobile device init.
   */
  private __device: mediasoup.Device;

  /**
   * MediaSoup Transport Instance, which handles the media transport.
   */
  private __mediasoupTransport: mediasoup.types.Transport;

  /**
   * Socket Instance, which handles the socket connection.
   */
  private readonly __socket: Socket;

  /**
   * Map of Producers, which handles the producers. ( Sending out Media Streams )
   *
   * `Mapped with {producerId => Producer}`
   */
  private readonly __producers: Map<string, Producer> = new Map();

  /**
   * Map of DataProducers, which handles the dataProducers. ( Sending out Data )
   *
   * `Mapped with {label => DataProducer}`
   */
  private readonly __dataProducers: Map<string, DataProducer> = new Map();

  /**
   * Map of DataConsumers, which handles the dataConsumers. ( Receiving Media Streams )
   *
   * `Mapped with {label:label => DataConsumer}`
   */
  private readonly __dataConsumers: Map<string, DataConsumer> = new Map();

  /**
   * Map of Consumers, which handles the consumers. ( Receiving Media Streams )
   *
   * `Mapped with {label:RemotePeerId => Consumer}`
   */
  private readonly __consumers: EnhancedMap<Consumer> = new EnhancedMap({});

  /**
   * Map of Identifiers to Producer Ids, which handles the mapping of identifiers to producer ids.
   *
   * `identifiers` are the unique identifiers for the stream, which is used to identify the stream.
   */
  public readonly labelToProducerId: Map<string, string> = new Map();

  /**
   * Transport Type, which handles the transport type. ( `send | recv` )
   */
  public readonly transportType: TTransportType;

  /**
   * Pending Producer Tasks, which handles the pending producer tasks.
   * callback function is necessary to be called when the producer is created
   * on the server as well as on the client side.
   */
  private __pendingProducerTasks: Map<
    string,
    ({
      id,
    }: {
      id: string;
    }) => void
  > = new Map();

  /**
   * Debounce to handle concurrent request to restart Ice. Waits for some time before sending
   * more requests to restart ice.
   */
  private __iceRestartDebounce = false;

  public get device() {
    return this.__device;
  }

  public get mediasoupTransport() {
    return this.__mediasoupTransport;
  }

  public get connectionState() {
    return this.__mediasoupTransport.connectionState;
  }

  public get producers() {
    return this.__producers;
  }

  public get dataProducers() {
    return this.__dataProducers;
  }

  public get dataConsumers() {
    return this.__dataConsumers;
  }

  public get consumers() {
    return this.__consumers;
  }

  public getProducerById(producerId: string): Producer {
    const producer = this.__producers.get(producerId);

    if (!producer) throw new Error('Producer not found');

    return producer;
  }

  public removeProducerById(producerId: string) {
    logger.info(`üîî Removing Producer, id: ${producerId}`);

    return this.__producers.delete(producerId);
  }

  /**
   * Get the consumer by label and peerId
   * @param data
   * @returns Consumer | null; Returns null if consumer is not found
   */
  public getConsumer = (data: {
    label: string;
    peerId: string;
  }): Consumer | null => {
    const consumer = this.__consumers.get(data.label, data.peerId);

    if (!consumer) return null;

    return consumer;
  };

  public get transport(): mediasoup.types.Transport {
    const transport = this.__mediasoupTransport;

    if (!transport) throw new Error('Transport Not Initialized');

    return transport;
  }

  public addPendingProducerTask = (data: {
    label: string;
    peerId: string;

    callback: ({
      id,
    }: {
      id: string;
    }) => void;
  }) => {
    const key = `${data.peerId}-${data.label}`;

    logger.info('üîî Adding Pending Producer Task, key', key);

    if (this.__pendingProducerTasks.has(key)) {
      logger.debug('üî¥ Producer Creation is Pending for key: ', key);

      return;
    }

    this.__pendingProducerTasks.set(key, data.callback);
  };

  public resolvePendingProducerTask = (data: {
    label: string;
    peerId: string;
    id: string;
  }) => {
    const key = `${data.peerId}-${data.label}`;

    logger.info('üîî Resolving Pending Producer Task, key', key);

    const callback = this.__pendingProducerTasks.get(key);

    if (!callback) {
      logger.error('üî¥ Producer Creation is not Pending for key: ', key);

      return;
    }

    callback({ id: data.id });

    this.__pendingProducerTasks.delete(key);
  };

  public static create = (data: {
    peerId: string;
    sdpInfo: TSDPInfo;
    device: mediasoup.Device;
    iceServers: RTCIceServer[];
    transportType: TTransportType;
    socket: Socket;
  }) => {
    try {
      logger.info(
        `üîî Creating Client Side Transport, type: ${data.transportType}`,
      );

      const { transportType, device } = data;

      const payload: TransportOptions = {
        id: data.sdpInfo.id,
        iceParameters: data.sdpInfo.iceParameters,
        iceCandidates: data.sdpInfo.iceCandidates,
        iceServers: data.iceServers,
        dtlsParameters: data.sdpInfo.dtlsParameters,
        sctpParameters: data.sdpInfo.sctpParameters,
        proprietaryConstraints: {},
        appData: {},
      };

      const mediasoupTransport =
        transportType === 'send'
          ? device.createSendTransport(payload)
          : device.createRecvTransport(payload);

      const transport = new Transport({
        peerId: data.peerId,
        device: data.device,
        transportType: data.transportType,
        mediasoupTransport,
      });

      return transport;
    } catch (error) {
      logger.error(`‚ùå Transport.create(), type: ${data.transportType}`);
      logger.error(error);

      throw error;
    }
  };

  private constructor(data: {
    peerId: string;
    device: mediasoup.Device;
    transportType: TTransportType;
    mediasoupTransport: mediasoup.types.Transport;
  }) {
    super();

    this.__socket = Socket.getInstance();

    this.__device = data.device;

    this.transportType = data.transportType;

    this.__mediasoupTransport = data.mediasoupTransport;

    this.__mediasoupTransport.on('connectionstatechange', (state) => {
      this.__connectionStateChangeHandler(state);
    });

    this.peerId = data.peerId;

    this.__listenTransportConnect();

    this.__listenTransportProduce();

    if (this.transportType === 'send') this.__listenTransportDataProduce();

    logger.info(`‚úÖ ${data.transportType} Transport Initialized`);
  }

  private __listenTransportConnect = () => {
    this.__mediasoupTransport.on(
      'connect',
      ({ dtlsParameters }, callback, errback) => {
        logger.info('üîî Transport Connect Event Called');

        try {
          this.once('connectTransportResponse', () => {
            callback();
          });

          this.__socket.publish('connectTransport', {
            dtlsParameters,
            transportType: this.transportType,
          });
        } catch (error) {
          logger.error('‚ùå Error Transport Connect Event');
          logger.error(error);
          errback(error as Error);
        }
      },
    );
  };

  private __listenTransportProduce = () => {
    this.__mediasoupTransport.on(
      'produce',
      async ({ kind, rtpParameters, appData }, callback, errback) => {
        logger.info(`üîî ${this.transportType} Produce Event Called`);

        try {
          const label = appData?.label as string;

          if (!label) throw new Error('üî¥ Stream Identifier Not Found');

          this.__socket.publish('produce', {
            createProducerData: {
              rtpParameters,
              kind,
              label,
              appData,
              paused: false,
            },
          });

          this.addPendingProducerTask({
            peerId: this.peerId,
            label,
            callback,
          });
        } catch (error) {
          logger.error('‚ùå Error Transport Produce Event');
          logger.error(error);
          errback(error as Error);
        }
      },
    );
  };

  private __listenTransportDataProduce = () => {
    logger.info(`üîî producedata: ${this.transportType} `);

    this.__mediasoupTransport.on(
      'producedata',
      async (
        { label, appData, sctpStreamParameters, protocol },
        callback,
        errback,
      ) => {
        logger.info(`üîî ${this.transportType} Produce Data Event Called`);
        try {
          this.__socket.publish('produceData', {
            createDataProducerData: {
              transportId: this.__mediasoupTransport.id,
              sctpStreamParameters,
              label: label as string,
              protocol,
              appData: appData as Record<string, string>,
            },
          });

          this.addPendingProducerTask({
            peerId: this.peerId,
            label: label as string,
            callback,
          });

          // this.__socket.subscribe('produceDataResponse', (data) => {
          //   logger.info('üîî Produce Data Response Received');
          //   callback({ id: data.dataProducerId });
          // });
        } catch (error) {
          logger.error('‚ùå Error Transport Produce Data Event');
          logger.error(error);
          errback(error as Error);
        }
      },
    );
  };

  public produce = async (data: {
    stream: MediaStream;
    label: string;
    stopTrackOnClose: boolean;
    appData?: AppData;
  }) => {
    const kind = getMediaStreamKind(data.stream);

    const track = getMediaTrack({ stream: data.stream, kind });

    logger.info(`üîî Produce Called for kind: ${kind}, label: ${data.label}`);

    try {
      if (!this.__device.loaded) {
        throw new Error('Device Not Loaded');
      }

      if (!this.__device.rtpCapabilities.codecs) {
        throw new Error('No Codecs Found');
      }

      if (!this.__device.canProduce(kind)) {
        throw new Error(`Device Cannot produce ${kind}`);
      }

      if (this.transportType !== 'send') {
        throw new Error(`Cannot produce on ${this.transportType} transport`);
      }

      const codecs = this.__device.rtpCapabilities?.codecs;
      if (!codecs) {
        throw new Error('‚ùå Device RTP Capabilities not found');
      }

      const codecViaMediaType = {
        video: codecs.find(
          (codec) => codec.mimeType.toLowerCase() === 'video/h264',
        ),
        'screen-share-video': codecs.find(
          (codec) => codec.mimeType.toLowerCase() === 'video/h264',
        ),
        audio: undefined,
      };

      const mediaType =
        data.label === 'screen-share-video' ? 'screen-share-video' : kind;
      const mediasoupProducer = await this.__mediasoupTransport.produce({
        track: track,
        encodings: encodingViaMediaType[mediaType],
        codecOptions: codecOptionsViaKind[kind],
        codec: codecViaMediaType[mediaType],
        stopTracks: data.stopTrackOnClose,
        zeroRtpOnPause: true,
        disableTrackOnPause: true,
        appData: {
          ...data.appData,
          producerPeerId: this.peerId,
        },
      });

      this.__producers.set(mediasoupProducer.id, mediasoupProducer);

      this.labelToProducerId.set(data.label, mediasoupProducer.id);

      logger.info(`üîî Producer Created sucessfully with label : ${data.label}`);

      return mediasoupProducer;
    } catch (error) {
      logger.error('‚ùå Error Transport Produce Event');
      logger.error(error);

      throw error;
    }
  };

  public produceData = async (data: {
    label: string;
    appData?: AppData;
  }) => {
    logger.info(`üîî Produce Data Called for label: ${data.label}`);

    try {
      if (this.transportType !== 'send') {
        throw new Error(`Cannot produce on ${this.transportType} transport`);
      }

      const dataProducer = await this.__mediasoupTransport.produceData({
        label: data.label,
        appData: {
          ordered: true,
          ...data.appData,
          producerPeerId: this.peerId,
          label: data.label,
        },
      });

      this.__dataProducers.set(dataProducer.label, dataProducer);

      return dataProducer;
    } catch (error) {
      logger.error('‚ùå Error Transport Produce Data Event');
      logger.error(error);

      throw error;
    }
  };

  public consume = async (data: TConsumeResponseData) => {
    const { label, producerPeerId, kind } = data;

    logger.info(
      `üîî Consume Called for ${kind} from remote peer ${producerPeerId}`,
    );
    try {
      if (this.transportType !== 'recv') {
        throw new Error(`Cannot consume on ${this.transportType} transport`);
      }

      const consumer = Consumer.create({
        producerId: data.producerId,
        producerPeerId,
        label,
      });

      if (!this.__device.loaded) {
        throw new Error('Device Not Loaded');
      }

      if (!this.__device.rtpCapabilities.codecs) {
        throw new Error('No Codecs Found');
      }

      const mediaSoupConsumer = await this.__mediasoupTransport.consume({
        id: data.consumerId,
        rtpParameters: data.rtpParameters,
        kind: data.kind,
        producerId: data.producerId,
        appData: data.appData,
      });

      mediaSoupConsumer.on('transportclose', () => {
        this.closeConsumer({ label, peerId: producerPeerId });
      });

      mediaSoupConsumer.on('trackended', () => {
        this.closeConsumer({ label, peerId: producerPeerId });
      });

      this.__consumers.set(consumer.label, consumer.producerPeerId, consumer);

      consumer.setMediaSoupConsumer(mediaSoupConsumer);

      return { consumer, mediaSoupConsumer };
    } catch (error) {
      logger.error(error);

      throw new Error('‚ùå Error calling consume()');
    }
  };
  public consumeData = async (data: TConsumeDataResponseData) => {
    const {
      label,
      appData,
      dataProducerId,
      protocol,
      id,
      peerId,
      sctpStreamParameters,
    } = data;

    logger.info(
      `üîî ConsumeData from producer ${peerId} consumerIdFromServer:${id}`,
    );
    try {
      if (this.transportType !== 'recv') {
        throw new Error(`Cannot consume on ${this.transportType} transport`);
      }

      if (!this.__device.loaded) {
        throw new Error('Device Not Loaded');
      }

      if (!this.__device.rtpCapabilities.codecs) {
        throw new Error('No Codecs Found');
      }

      const dataConsumer = await this.transport.consumeData({
        id,
        dataProducerId,
        sctpStreamParameters,
        label,
        protocol,
        appData,
      });

      dataConsumer.on('open', () => {
        logger.info(`‚úÖ DataConsumer with ${label} opened`);
      });

      dataConsumer.on('close', () => {
        logger.warn(`‚úÖ DataConsumer with ${label} closed`);

        this.__dataConsumers.delete(label);
      });

      dataConsumer.on('error', (error) => {
        logger.error(`‚úÖ DataConsumer "error": ${error} closed`);
      });

      this.__dataConsumers.set(label, dataConsumer);

      return dataConsumer;
    } catch (error) {
      logger.error(error);

      throw new Error('‚ùå Error calling consumeData()');
    }
  };

  public closeConsumer = (data: { label: string; peerId: string }) => {
    try {
      const consumer = this.getConsumer(data);

      if (!consumer) {
        throw new Error(
          `‚ùå Consumer not found label: ${data.label}, peerId: ${data.peerId}`,
        );
      }

      consumer.close();

      this.__consumers.delete(data.label, data.peerId);
    } catch (error) {
      logger.error('‚ùå Error closing consumer');
      logger.error(error);
    }
  };

  public close = async (data: { retries: number }) => {
    try {
      if (data.retries <= 0) {
        logger.error('‚ùå Error closing transport, max retries exceeded');
        return;
      }

      logger.info(`üîî Closing ${this.transportType} transport`);

      this.__mediasoupTransport.close();

      logger.info(`‚úÖ ${this.transportType} transport closed`);
    } catch (error) {
      logger.error('‚ùå Error closing transport');
      logger.error(error);
      logger.error('Retrying...');

      this.close({ retries: data.retries - 1 });
    }
  };
  private __connectionStateChangeHandler = (state: ConnectionState) => {
    try {
      logger.debug(
        `üîî ${this.transportType} Transport Connection State Changed, state: ${state}`,
      );
      const transportType = this.transportType;
      const handler: Record<ConnectionState, () => void> = {
        connected: () => {
          logger.debug(`üîî ${this.transportType} Transport Connected`);
        },
        disconnected: () => {
          if (this.__iceRestartDebounce) return;

          this.__iceRestartDebounce = true;

          this.__socket.publish('restartTransportIce', {
            transportId: this.__mediasoupTransport.id,
            transportType,
          });

          setTimeout(() => {
            this.__iceRestartDebounce = false;
          }, 3000);
          logger.debug(`üîî ${transportType} Transport Disconnected`);
        },
        failed: () => {
          logger.debug(`üîî ${transportType} Transport Failed`);
        },
        connecting: () => {
          logger.debug(`üîî ${transportType} Transport Connecting`);
        },
        closed: () => {
          logger.debug(`üîî ${transportType} Transport closed`);
        },
        new: () => {
          logger.debug(`üîî ${transportType} Transport new`);
        },
      };

      handler[state]();
    } catch (err) {
      logger.error('‚ùå Error in connectionStateChangeHandler');
      logger.error(err);
    }
  };
}

export default Transport;
